main {


    section {
        min-height: 100dvh;
    }

    section#technologies {
        background: #ddd;


        .tech {
            padding: 1rem;
            display: flex;
            flex-wrap: wrap;
            gap: 1rem;

            .language-box {
                background-color: white;
                border: 1px solid #ddd;
                border-radius: 8px;
                padding: 2rem 3rem;
                text-align: center;
                box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
                transition: transform 0.3s ease, box-shadow 0.3s ease;
                cursor: pointer;

                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
                }

                img {
                    width: 100px;
                    object-fit: cover;
                    margin-bottom: 10px;
                }

                h3 {
                    margin: 0;
                    font-size: 18px;
                }
            }
        }

    }

    section#resources {



        ul {
            display: flex;
            list-style: none;
            gap: 2rem;
            align-items: start;



            li {
                background: rgb(233, 237, 237);
                padding: 1rem;
                border-radius: 10px;
                transition: .2s all;

                box-shadow: 2px 2px 7px #313f626b;
                max-width: 280px;

                &:hover {
                    scale: 1.05;
                    background: rgb(193, 229, 255);
                }

                a {
                    text-decoration: none;
                    color: #000;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;

                    h3 {
                        margin: 0;
                        color: #8f8f8f;
                        font-size: 1rem;
                    }

                    img.thumbnail {
                        width: 100%;
                        border-radius: 10px;
                    }


                    .author_info {

                        display: flex;
                        gap: .5rem;
                        align-items: center;

                        img {
                            border-radius: 50%;
                            width: 30px;
                            height: 30px;
                        }

                    }

                }
            }
        }
    }
}